import React, { Suspense, useState } from "react";
import { CreateBracket, ProcessBracket } from "../../util/CookieResultManager";
import { CookiesProvider, useCookies } from "react-cookie";
import Round from "./Round";
import TeamData from "../../data/team-data";
import "./Bracket.css";

const Confetti = React.lazy(() => import("react-confetti"));

const Bracket = (props) => {
  const bracket = CreateBracket();
  const [updatedBracket, setBracket] = useState(bracket);
  const [cookie, setCookie] = useCookies(["result"]);
  function setBracketHandler(bracket) {
    let result = cookie.result;
    result = ProcessBracket(bracket);
    setCookie("result", result, { path: "/" });
    setBracket(bracket);
  }

  const champIdOnLoad = bracket[3][0].team1.selected
    ? TeamData[bracket[3][0].team1.id]
    : bracket[3][0].team2.selected
    ? TeamData[bracket[3][0].team2.id]
    : false;

  console.log(champIdOnLoad);

  const [champ, setChamp] = useState(champIdOnLoad);
  const setChampHandler = (teamId) => {
    setChamp(TeamData[teamId]);
  };

  const bracketDOMElement = document.getElementById("bracket")
    ? document.getElementById("bracket")
    : { scrollWidth: 2000, scrollHeight: 1500 };
  console.log(champ);

  return (
    <CookiesProvider>
      {champ ? (
        <Suspense fallback={<div></div>}>
          <Confetti
            width={bracketDOMElement.scrollWidth}
            height={bracketDOMElement.scrollHeight}
            numberOfPieces={1000}
            colors={champ.colors}
            tweenDuration={1000}
          />
        </Suspense>
      ) : null}
      <div className="bracket flex flex-nowrap flex-row" id="bracket">
        {updatedBracket.map((round, i) => {
          let name = "";
          let className = "";
          if (i === 0) {
            name = "Round of 16";
            className = "rd16";
          } else if (i === 1) {
            name = "Quarterfinals";
            className = "quarterfinals";
          } else if (i === 2) {
            name = "Semifinals";
            className = "semifinals";
          } else if (i === 3) {
            name = "Final";
            className = "final";
          }

          return (
            <Round
              name={name}
              class={className}
              round={i}
              key={i}
              bracket={updatedBracket}
              setBracket={setBracketHandler}
              teamsSelected={props.teamsSelected}
              setTeamsSelected={props.setTeamsSelected}
              setChampHandler={setChampHandler}
            />
          );
        })}
        <div className="pr-10"></div>
      </div>
    </CookiesProvider>
  );
};

export default Bracket;
