import Match from "./Match";

const Section = (props) => {
  const matches = props.matches.map((match) => {
    return match ? (
      <Match
        match={match}
        key={match.id}
        round={props.round}
        bracket={props.bracket}
        setBracket={props.setBracket}
        teamsSelected={props.teamsSelected}
        setTeamsSelected={props.setTeamsSelected}
        setChampHandler={props.setChampHandler}
      />
    ) : (
      <Match key={match.id} />
    );
  });

  return (
    <div className="section flex mb-7">
      <div className="matchups flex flex-col justify-between w-full">
        {matches}
      </div>
      <div className="connector flex items-center">
        <div className="merger relative w-8 inline-block align-top border-y-2 border-r-2 border-slate-400"></div>
        <div className="line -mr-5 w-8 inline-block align-top border-b-2 border-slate-400"></div>
      </div>
    </div>
  );
};

export default Section;
