import Team from "../Team";
import PlaceHolderTeam from "../../util/PlaceHolderTeam";
import "./Match.css";

const Match = (props) => {
  const bracket = [...props.bracket];
  const selectedHandler = (team) => {
    // only allow to select if the match is set
    if (
      props.match.team1.name !== "Placeholder" &&
      props.match.team2.name !== "Placeholder"
    ) {
      // set as selected
      if (team.id === props.match.team1.id) {
        team.selected = !team.selected;
        props.match.team1 = team;
        if (team.selected && props.match.team2.selected) {
          props.match.team2.selected = !props.match.team2.selected;
        }
      } else {
        team.selected = !team.selected;
        props.match.team2 = team;
        if (team.selected && props.match.team1.selected) {
          props.match.team1.selected = !props.match.team1.selected;
        }
      }

      // update selected match
      bracket[props.round][props.match.id] = props.match;

      // update the next round, unless this is the final round
      if (props.round < 3) {
        const nextRound = [...bracket[props.round + 1]];

        // set selected team in next rounds correct spot
        if (props.match.id % 2 === 0) {
          if (team.selected) {
            nextRound[Math.floor(props.match.id / 2)].team1 = { ...team };
          } else {
            nextRound[Math.floor(props.match.id / 2)].team1 = PlaceHolderTeam;
          }
        } else {
          if (team.selected) {
            nextRound[Math.floor(props.match.id / 2)].team2 = { ...team };
          } else {
            nextRound[Math.floor(props.match.id / 2)].team2 = PlaceHolderTeam;
          }
        }

        // clear previously selected match if team changed
        nextRound[Math.floor(props.match.id / 2)].team1.selected = false;
        nextRound[Math.floor(props.match.id / 2)].team2.selected = false;

        // set next round
        bracket[props.round + 1] = nextRound;

        // if this is the 1st or 2nd round update all the way down the bracket
        if (props.round <= 1) {
          // update 2 rounds ahead
          if (Math.floor(props.match.id / 2) % 2 === 0) {
            // only clear this teams spot and unselect the opp
            bracket[props.round + 2][Math.floor(props.match.id / 4)].team1 =
              PlaceHolderTeam;
            bracket[props.round + 2][
              Math.floor(props.match.id / 4)
            ].team2.selected = false;
          } else {
            // only clear this teams spot and unselect the opp
            bracket[props.round + 2][Math.floor(props.match.id / 4)].team2 =
              PlaceHolderTeam;
            bracket[props.round + 2][
              Math.floor(props.match.id / 4)
            ].team1.selected = false;
          }

          // update 3 rounds ahead
          if (props.round === 0) {
            if (Math.floor(props.match.id / 4) % 2 === 0) {
              // only clear this teams spot and unselect the opp
              bracket[props.round + 3][0].team1 = PlaceHolderTeam;
              bracket[props.round + 3][0].team2.selected = false;
            } else {
              // only clear this teams spot and unselect the opp
              bracket[props.round + 3][0].team2 = PlaceHolderTeam;
              bracket[props.round + 3][0].team1.selected = false;
            }
          }
        }
      }
      // update the entire bracket
      props.setBracket(bracket);
    }

    let teamsSelected = 0;
    bracket.flat().map((match) => {
      if (match.team1.selected) {
        teamsSelected++;
      }
      if (match.team2.selected) {
        teamsSelected++;
      }
      return null;
    });
    props.setTeamsSelected(teamsSelected);
    console.log(teamsSelected);
    if (teamsSelected === 15) {
      props.setChampHandler(team.id);
    } else {
      props.setChampHandler(false);
    }
  };

  const team1 =
    props.match && props.match.team1 ? props.match.team1 : PlaceHolderTeam;
  const team2 =
    props.match && props.match.team2 ? props.match.team2 : PlaceHolderTeam;

  return (
    <div className="match">
      <div className="teams border border-slate-300 rounded-tl-3xl rounded-br-3xl shadow-xl">
        <Team team={team1} onSelected={selectedHandler} size="small" />
        <Team team={team2} onSelected={selectedHandler} size="small" />
      </div>
    </div>
  );
};

export default Match;
