import Section from "./Section";

const Round = (props) => {
  const sections = [];
  const matches = props.bracket[props.round];
  for (let i = 0; i < matches.length; i = i + 2) {
    const section = [];
    if (matches.length === 1) {
      section.push(matches[i]);
    } else {
      section.push(matches[i]);
      section.push(matches[i + 1]);
    }
    sections.push(section);
  }
  return (
    <div
      className={`round ${props.class} w-full shrink-0 border-dashed border-r border-slate-100 px-2`}
    >
      <div className="text-xl font-bold mb-2 text-slate-600">{props.name}</div>
      {sections.map((section, i) => (
        <Section
          matches={section}
          key={i}
          round={props.round}
          bracket={props.bracket}
          setBracket={props.setBracket}
          teamsSelected={props.teamsSelected}
          setTeamsSelected={props.setTeamsSelected}
          setChampHandler={props.setChampHandler}
        />
      ))}
    </div>
  );
};

export default Round;
